@import url('game-page.css');
@import url('result-page.css');

@font-face {
    font-family: stratosskyengweb-regular;
    src: url('static/stratosskyengweb-regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: stratosskyengweb-regular, sans-serif;
    background-color: #048;
}

.container {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.overlay {
    position: fixed;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.selection-form {
    width: 480px;
    height: 460px;
    border-radius: 12px;
    background-color: #c2f5ff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__header {
        font-size: 40px;
        color: #004980;
        text-align: center;
        margin-bottom: 48px;
    }

    &__card-container {
        display: flex;
        flex-direction: row;
        width: 342px;
        justify-content: space-between;
        margin-bottom: 67px;

        input[type='radio'] {
            display: none;
        }

        label {
            width: 98px;
            height: 98px;
            border-radius: 12px;
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #0080c1;
            font-size: 64px;
            cursor: pointer;
        }

        input[type='radio']:checked + label {
            background-color: #7ac100;
            color: #fff;
        }
    }

    &__start-button {
        width: 246px;
        height: 50px;
        color: #fff;
        background-color: #7ac100;
        border-radius: 12px;
        border-color: transparent;
        font-size: 24px;
        font-family: stratosskyengweb-regular, sans-serif;

        &:hover {
            cursor: pointer;
        }
    }
}
